<template>
  <h1 style="text-align: center">请选择医生</h1>
  <el-row justify="center">
    <el-col style="max-width: 400px">
      <el-table
        :data="dept_sch_for_doc"
        class="DeptSchForDocView"
        @row-click="ToGetDocSch"
      >
        <el-table-column label="医生名称" prop="docName"></el-table-column>
      </el-table>
    </el-col>
  </el-row>
</template>

<script>
import { Store } from "@/stores";
import { API, Config } from "@/utils/API";
import { ElMessage } from "element-plus";
import { getPhsSign } from "@/utils/Func";

const store = Store();

export default {
  name: "DeptSchForDocView",
  data() {
    return {
      dept_sch_for_doc: [],
    };
  },
  methods: {
    get_dept_sch_for_doc() {
      const headers = {
        phsSign: getPhsSign(),
        phsId: Config.phsid,
        "Content-Type": "application/json",
      };
      const data = {
        args: {
          clinicalType: "1",
          deptId: store.Dept.deptCode,
          specialtyId: "3",
          deptName: store.Dept.deptName,
          visitingArea: "",
          hosId: store.HospitalZone.hosId,
          recommendation: "",
          type: "order",
          source: 22,
          sysCode: store.HospitalZone.sysCode,
        },
        token: store.token,
      };
      this.axios
        .post(API.get_dept_sch_for_doc, data, { headers })
        .then((res) => {
          if (res.data.code === "0") {
            console.log(res.data.result.schDocResultList);
            this.dept_sch_for_doc = res.data.result.schDocResultList;
          } else {
            ElMessage.error(res.data.message);
          }
        });
    },
    ToGetDocSch(item) {
      store.Doctor = item;
      console.log(item);
      this.$router.push("/doctordetails");
    },
  },
  mounted() {
    this.get_dept_sch_for_doc();
  },
};
</script>

<style scoped></style>
